import {TasksActionsType, tasksReducer} from './tasks-reducer'
import {TodolistsActionsType, todolistsReducer} from './todolists-reducer'
import {applyMiddleware, combineReducers, compose, legacy_createStore as createStore} from 'redux'
import thunk, {ThunkAction, ThunkDispatch} from "redux-thunk";
import {useDispatch} from "react-redux";
import {appReducer, AppReducerActionsType} from "./app-reducer";
import {authReducer} from "./auth-reducer";

//     Redux
declare global {
    interface Window {
        __REDUX_DEVTOOLS_EXTENSION_COMPOSE__?: typeof compose;
    }
}
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;


//  reducer-   combineReducers,
//      -
const rootReducer = combineReducers({
    tasks: tasksReducer,
    todolists: todolistsReducer,
    app: appReducer,
    auth: authReducer,
})

//   store
export const store = createStore( // export const store = legacy_createStore(
    rootReducer,
    compose(applyMiddleware(thunk),
        composeEnhancers())  // export const store = createStore(rootReducer,applyMiddleware(thunk)) if only one enhancer (enhancer=)
)

//      
// export type AppRootStateType = ReturnType<typeof rootReducer> //   Dispatch
export type AppRootStateType = ReturnType<typeof store.getState> //   Dispatch

//    Action
export type AppActionType = TodolistsActionsType | TasksActionsType | AppReducerActionsType

export type AppDispatch = ThunkDispatch<AppRootStateType, unknown, AppActionType>;


export const useAppDispatch: () => AppDispatch = useDispatch

//  Thunk Action   
export type AppThunk<ReturnType = void> = ThunkAction<ReturnType, AppRootStateType, unknown, AppActionType>;


//  ,         store   
// @ts-ignore
window.store = store